.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.mockCha {
  writing-mode: vertical-lr;
  position: fixed;
  background: rgb(85, 150, 85);
  border: none;
  width: 40px;
  height: 100px;
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
  border-top-right-radius: 11px;
  border-bottom-right-radius: 11px;
  padding-left: 5px;
  font-family: "jamsil";
}

.mockCha:hover {
  background-color: rgb(51, 119, 51);
}

.title {
  margin-left: 80px;
  font-size: 25px;
  font-weight: bold;
  font-family: "jamsil";
}

.bodyList {
  font-family: "nanum";
  height: 30px;
  font-size: 22px;
  padding-bottom: 35px;
  margin-bottom: 20px;
  border-bottom: 1px solid lightgray;
}

.bodyList:hover {
  text-decoration: underline;
  text-underline-offset: 6px;
  cursor: pointer;
}

.projectDetail {
  margin-left: 30px;
  font-family: "nanum";
  font-size: 13px;
}

.projectDetail:hover {
  text-decoration: underline;
  text-underline-offset: 4px;
  cursor: pointer;
}